<template>
  <div class="simple-crop">
    <div class="bottom">
      <img ref="cropImg" :src="imgSrc" style="display:block;max-width:50%" @load="begin">
      <div class="btn">
        <div @click="end">
          <slot name="end"></slot>
        </div>
        <div @click="cancel">
          <slot name="cancel"></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
import Cropper from 'cropperjs'
import 'cropperjs/dist/cropper.css';

export default {
  props: ['imgSrc', 'config','filename'],
  data () {
    return {
      isBegin: false,
      cropper: null,
    }
  },
  methods: {
    begin () {
      this.isBegin = true;
      var img = this.$refs.cropImg;
      var bodyH = document.body.offsetHeight;
      var bodyW = document.body.offsetWidth;
      console.dir(img);
      var defaultOption = {
        aspectRatio: 17 / 10,
        minContainerWidth: bodyW,
        minContainerHeight: bodyH,
        // viewMode:1,
        preview: '.showImg',
        crop (event) {
          console.log(event);
          console.log(event.detail.y);
          console.log(event.detail.width);
          console.log(event.detail.height);
          console.log(event.detail.rotate);
          console.log(event.detail.scaleX);
          console.log(event.detail.scaleY);
        },
      }
      var options = Object.assign(defaultOption, this.config.options);
      console.log(options);
      this.cropper = new Cropper(img, options)
    },
    end () {
      console.log('end');
      this.cropper.getData();
      console.dir(this.cropper.getCroppedCanvas())
      this.cropper.getCroppedCanvas().toBlob((blob) => {
        console.log(blob);
        const formData = new FormData();
        formData.append('croppedImage', blob);
        console.log(formData.get('croppedImage'))
        if (this.config.Compression.isTrue) {
          this.beforeCompression(formData.get('croppedImage'));     //
        } else {
          this.$emit('post', formData.get('croppedImage'))      //
        }
      })
    },
    beforeCompression (file) {
      var maxWidth = 300;
      // var file = formData.get('croppedImage');
      var reader = new FileReader(file);

      console.log(file);
      console.log(reader);
      //
      if (file.type.split('/')[0] !== 'image') {
        alert('you should choose an image file');
        return;
      }

      //onload,,
      reader.onload = () => {
        const ONE_MB = 1024 * 1024;
        const img_data = reader.result;
        // var imgEle = img_data; //this.$refs.imgElement;
        var image = new Image();		  // imagecanvas
        image.src = reader.result;		  // this.resultbase64
        image.onload = () => {

          if (img_data.length > 0) {
            /*
           * img_database64
           *  imgsrc
           *
           */
            if (file.size > ONE_MB) {
              //
            }
            // //
            // this.header_img = img_data;
            // // this.fileUpload = file;
            // this.$nextTick(() => {
            //   this.option.img = img_data;
            //   this.isShowCropper = true
            //   console.log(this.option.img)
            // })

            this.handleCompress(image, maxWidth, file.type, 0);
          }
        };
      }
      reader.readAsDataURL(file);
    },
    handleCompress (img, maxWidth, mimeType, rotateDeg) {
      //
      var cvs = document.createElement('canvas');
      var width = img.naturalWidth,
        height = img.naturalHeight,
        imgRatio = width / height;
      //
      if (width > maxWidth) {
        width = maxWidth;
        height = width / imgRatio;
      }
      cvs.width = width;
      cvs.height = height;
      var ctx = cvs.getContext("2d");
      var destX = 0,
        destY = 0;
      if (rotateDeg) {
        ctx.translate(cvs.width / 2, cvs.height / 2);
        ctx.rotate(rotateDeg);
        destX = -width / 2;
        destY = -height / 2;
      }
      ctx.drawImage(img, 0, 0, img.naturalWidth, img.naturalHeight, destX, destY, width, height);
      //
      var quality = width >= 1200 ? 0.6 :
        width > 600 ? 0.8 : 1;
      //base64
      var newImageData = cvs.toDataURL(mimeType, quality);
      var newFile = this.dataURLtoFile(newImageData,this.filename)
      // var resultImg = new Image();
      console.log(newFile);
      this.$emit('post', newFile.get('croppedImage'));
      // this.new_img = newImageData;
      // resultImg.src = newImageData;
      // this.isClick = false
      // return resultImg;
      // console.log(resultImg);
    },
    dataURLtoFile (dataurl, filename) { //base64

      var arr = dataurl.split(','),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);

      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      const formData = new FormData();
      formData.append('croppedImage', new File([u8arr], filename, {
        type: mime
      }));
      return formData;

    },
    convertBase64UrlToBlob (urlData) {
      let bytes = window.atob(urlData.split(',')[1]);//urlbyte
      //,ascii00
      let ab = new ArrayBuffer(bytes.length);
      let ia = new Uint8Array(ab);
      for (var i = 0; i < bytes.length; i++) {
        ia[i] = bytes.charCodeAt(i);
      }
      const formData = new FormData();
      formData.append('croppedImage', new Blob([ab], { type: 'image/jpeg' }));
      return formData;
    },
    cancel () {
      this.$emit('handleCancel')
    }
  },
}
</script>

<style scoped>
/* .simple-crop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3000;
} */
#cropEndBtn {
  position: absolute;
  bottom: 0;
  z-index: 3001;
}
.bottom {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
/deep/ .cropper-container {
  height: 100%;
  width: 100%;
}
.showImg {
  width: 160px;
  height: 90px;
  overflow: hidden;
}
.btn {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 18px;
}
</style>
